@use "../../styles/index.scss" as *;

.common-layout {
  min-height: 100vh;
  min-height: 100dvh;
  display: flex;
  flex-direction: column;
  width: 100%;

  &__content {
    flex: 1;
    width: 100%;
    min-height: 0; // flexbox  

    &--with-bottom-dock {
      // BottomDock  :
      //  (10px) +  (10px  + 20px  + 4px gap + 10px  + 10px ) +  (10px + safe-area)
      // = 10px + 54px + 10px + safe-area = 74px + safe-area-inset-bottom
      padding-bottom: calc(#{px(74)} + env(safe-area-inset-bottom, 0px));
      
      @supports (padding: max(0px)) {
        padding-bottom: max(#{px(74)}, calc(#{px(74)} + env(safe-area-inset-bottom)));
      }
    }
  }
}

